import defaultImg from '@/static/mbcImg/publish/createBusiness/defaultImg.png'
import date from '@/static/mbcJs/dateTime.js';
export default{
	components:{
		
	},
	props:[
		'updataTime',
		'disabled',
		'routeParam',
		'dataList'
	],
	created() {
		this.users();
		this.isUserFollow();
	},
	data(){
		return{
			list:[],
			upDataTime:this.updataTime,
			defaultImg:defaultImg,
			userData:this.dataList.userRetVO,
			focusText: ''
		}
	},
	filters: {
	    /* 格式化时间戳 */
	    dateTime (val) {
	      return date.dateTime('', val);
	    },
	  },
	methods:{
		users(){
			if (uni.getStorageSync('landRegist')) {
				let landRegistLG = JSON.parse(uni.getStorageSync('landRegist')); // 读取缓存的用户信息
				console.log(landRegistLG.user.id);
				let params = {}; // 请求总数居时 参数为空
				uni.showLoading({ // 展示loading
					title: '加载中'
				});
				uni.request({
					url: this.api2 + '/rest-rp/user/'+landRegistLG.user.id, //接口地址。
					// data: this.endParams(params),
					method: 'GET',
					header: {
						Authorization: "Bearer " + landRegistLG.token //将token放到请求头中
					},
					success: (response) => {
						uni.hideLoading();
						console.log(response.data);
						this.list = response.data.content
						// this.$store.commit('setMation', this.List); // 更新vuex
						
					},
					fail: (error) => {
						uni.hideLoading(); // 隐藏 loading
						uni.showToast({
							title: '网络繁忙，请稍后',
							icon: 'none',
							duration: 1000
						});
						console.log(error, '网络繁忙，请稍后');
					}
				});
			}
		},
		isUserFollow(){
			if(this.userData.isUserFollow=='0'){
				this.focusText="+关注"
			}
			if(this.userData.isUserFollow=='1'){
				this.focusText="已关注"
			}
		},
		Focused(e){
			console.log(e,'点击了关注');
			if(uni.getStorageSync('landRegist')){
			     let landRegistLG = JSON.parse(uni.getStorageSync('landRegist')); // 读取缓存的用户信息
			     console.log(landRegistLG.user.id);
				 let parmas = {
				 	userId: landRegistLG.user.id,
				 	modelId: e,
				 	type: 4
				 };
				if(parmas.userId!=parmas.modelId){
					this.focusStore(parmas).then(res => {
						console.log(res);
						if (res.success === true) {
							this.focusText = '已关注'
						} else {
							this.focusText = '+关注'
						}
					})
					.catch(err => {
						console.log(err);
					});
				}else{
					uni.showToast({
						title: '自己不能关注自己哦',
						icon: 'none',
						duration: 1000
					});
				}
			}else if (!uni.getStorageSync('landRegist')) {
			 this.landRegistra(); // 判断登录状态
			}
		},
		
	}
}
